<template>
  <div class="search">

    <div class="search-header">
        <div class="search-back" @click="backClick">
            <van-icon class="back-icon" name="arrow-left" style="padding-left: .16rem;"/>
        </div>
        <van-search
            class="search-input"
            v-model="keyWord"
            show-action
            placeholder="请搜索您想看的影视"
            @search="onSearch"
            @cancel="onCancel"
        />
    </div>

    <div class="hishotlay" v-show="showHis">
      <van-sticky :offset-top="'1.08rem'">
        <van-row type="flex" justify="space-between" style="background: #fff;">
          <van-col span="6" class="hisleft-title">历史搜索</van-col>
          <van-col span="6" class="hisright-lay" @click="clearHisClick">
            清空<van-icon name="delete-o" />
          </van-col>
        </van-row>
        <div class="hiswords-lay" style="background: #fff;">
          <van-grid :column-num="3" :border="false" :gutter="'.06rem'" style="background: #fff;">
            <van-grid-item 
              class="his-word" 
              v-for="word of hisWords" 
              :key="word.wid"
              @click="hotSearch(word.word)"
            >
                <span class="word-button">
                  {{ word.word }}
                </span>
            </van-grid-item>
          </van-grid>
        </div>
        <div class="hottitle" style="background: #fff;">
          热门搜索
        </div>
      </van-sticky>
      
      <van-list
          :finished="true"
          finished-text="没有更多了"
          class="hotlist"
        >
        <span 
         v-for="(hotItem,index) of hotWords" :key="index"
         class="hot-item"
         @click="hotSearch(hotItem.word)"
        >
          {{ hotItem.word }}
        </span>
      </van-list>

    </div>


    <div class="lxclay" v-show="showLxc">
      <van-list
          :finished="true"
          finished-text="没有更多了"
          class="lxclist"
        >
        <span 
         v-for="(lxcItem,index) of lxcWords" :key="index"
         class="lxc-item"
         @click="hotSearch(lxcItem)"
         v-html="highlightItem(lxcItem)"
        >
          
        </span>
      </van-list>
    </div>

    <div class="vodslay" v-show="showVods">
      <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          class="vodslist"
          @load="onLoad"
        >
          <div 
            class="vod-class" 
            v-for="(vod,index) of computedVods" :key="index"
            @click="handleToDetail(vod.id)"
          >
            <img v-lazy="vod.imgy" class="fixed-size-image" />
            <div class="vod-info">
              <p class="name-class">{{ vod.name }}</p>
              <p class="year-class">{{ vod.computedYat }}</p>
              <p class="director-class">{{ vod.computedDirec }}</p>
            </div>
          </div>
      </van-list>
    </div>

  </div>  
  
</template>

<script>
import { Search, Icon, Col, Row, List, Grid, GridItem, Sticky,Dialog} from 'vant'
import 'vant/lib/search/style'
import 'vant/lib/icon/style'
import 'vant/lib/col/style'
import 'vant/lib/row/style'
import 'vant/lib/list/style'
import 'vant/lib/grid/style'
import 'vant/lib/sticky/style'
import 'vant/lib/dialog/style'
import 'vant/lib/grid-item/style'
import db from 'common/db.js'

export default {
   name:'SearchPage',
   components: {
      [Search.name]:Search,
      [Icon.name]:Icon,
      [Col.name]:Col,
      [Row.name]:Row,
      [List.name]:List,
      [Grid.name]:Grid,
      [GridItem.name]:GridItem,
      [Sticky.name]:Sticky,
      [Dialog.name]:Dialog,
   },
   data() {
    return {
      keyWord: '',
      vods: [],
      page: 1,
      hisWords:[],
      lxcWords:[],
      hotWords:[],
      loading: false,
      finished: false,
      showHis: true,
      showLxc: false,
      showVods: false,
      isClickHotHis:false,
    }
   },
   mounted () {
   },
   computed : {
    computedVods () {
      return this.vods.map(item => {
        return {
          id:item.id,
          imgy:item.imgy,
          name:item.name,
          computedYat: this.computedYatByItem(item),
          computedDirec: this.computedDirecByItem(item),
        }
      })
    }
   },
   watch: {
    keyWord () {
       console.log(`search: ${this.keyWord}`)
       if (this.keyWord === '') {
         this.page = 1
         this.lxcWords = []
         this.vods = []
         this.showLxc = false
         this.showVods = false
         this.showHis = true
         this.isClickHotHis = false
       } else {
         if(!this.isClickHotHis){
           this.getLxc()
         } else {
           this.getSearchData()
         }
       }
    },
    lxcWords (newList) {
      console.log(`this.lxcWords.length: ${this.lxcWords.length}`)
      if(newList.length > 0){
        this.showLxc = true
        this.showHis = false
        this.showVods = false
      }
    },
    vods (newList) {
      if(newList.length > 0) {
        this.showLxc = false
        this.showHis = false
        this.showVods = true
      }
    }
   },
   activated() {
      console.log("search--activated")
      this.keyWord = ''
      this.vods = []

      this.hotWords = []
      this.getHotWords()
      
      this.hisWords = []
      this.getHisWords()
      
      this.showHis = true
      this.showLxc = false
      this.showVods = false
      this.isClickHotHis = false
      this.page = 1
   },
   methods: {
    backClick () {
      this.$router.back(-1)
    },
    onLoad () {
      this.page += 1
      this.loading = true
      this.getSearchData()
    },
    onSearch(word) {
      console.log(`search-- ${word}`)
      this.getSearchData()
    },
    onCancel() {
      console.log('取消')
    },
    clearHisClick() {
      Dialog.confirm({
        title: '提示',
        message: '确认清空?',
      })
      .then(() => {
        db.searchword.clear().then(()=>{
            this.hisWords = []
        })
      })
      .catch(() => {
        // on cancel
      })
    },
    hotSearch(word) {
      this.isClickHotHis = true
      this.keyWord = word
    },
    getHisWords() {
      db.searchword.orderBy('wid').reverse().limit(10).toArray()
      .then(list => {
        // console.log(list)
        this.hisWords.push(...list)
      })
      .catch(error => {
        console.error(error)
      })
    },
    getHotWords() {
      this.$http.get(`/api/ssc`).then((res)=>{
        const result = res.data
        if(result.code === 1000 && result.data) {
          this.hotWords.push(...result.data)
        }
      })
    },
    getLxc() {
      this.$http.get(`/api/lxc?page=1&limit=100&w=${this.keyWord}`)
        .then((res)=>{
          const result = res.data
          if(result.code === 1000 && result.data) {
            this.lxcWords = []
            this.lxcWords.push(...result.data)
          }
        })
    },
    highlightItem(item) {
      const highlighted = this.keyWord
      // console.log(`highlighted: ${highlighted}`)
      const replacedText = item.replace(
        new RegExp(highlighted, 'g'),
        `<span style="color: #41ae3c;">${highlighted}</span>`
      )
      // console.log(`replacedText: ${replacedText}`)
      return replacedText
    },
    getSearchData() {
      db.searchword.where('word').equals(this.keyWord).toArray().then(list => {
        // console.log(list)
        if(list.length <= 0) {
          const sword = {
            word:this.keyWord,
            wid:Date.now()
          }
          this.hisWords.push(sword)
          db.searchword.put(sword).then(() => {
            console.log('Item was added or replaced successfully!')
          }).catch(e => {
            console.error('Error: ' + e)
          })
        }
      })
      this.$http.get(`/api/ss?page=${this.page}&limit=10&w=${this.keyWord}`)
       .then((res)=>{
         const result = res.data
         if(result.code === 1000 && result.data) {
           if(result.data.length > 0){
             this.vods.push(...result.data)
            //  console.log(`this.vods: ${this.vods.length}`)
           } else {
             this.finished = true
           }
         }
         this.loading = false
       })
    },
    handleToDetail (id) {
      this.$router.push({ path: `/detail/${id}` })
    },
    computedYatByItem(item) {
      if (item) {
        const year = item.year === "" ? "" : item.year
        const types = item.type.split(',')
        const type = types.length > 2 ? types.slice(0, 2).join("·") : types.join("·")
        const yatinfo = year + '︲'+item.area + '︲' + type
        return yatinfo
      } else {
        return ''
      }  
    },
    computedDirecByItem(item) {
      if (item) {
        const directors = item.director.split(',')
        let director = directors.length > 0 ? directors[0] : ""
        director = director === "" ? "" : `${director} ‣`
        let actors = `${item.actor}`.split(',')
        let isChinese = true
        if (actors.length > 0) {
            isChinese = actors[0].length <= 4
        }
        let actor = actors.length > 4 ? actors.slice(0, 4).join("▪") : actors.join("▪")
        if (!isChinese) {
            actor = actors.length > 2 ? actors.slice(0, 2).join("▪") : actors.join("▪")
        }
        director = `${director} ${actor === "未知" ? "未知" : actor}`
        item.direc = director
        return director
      } else {
        return ''
      }
    }
  },
}
</script>

